.page-title {
	padding: 3em 0;
	background: linear-gradient(33.13deg, #27A9F8 9.78%, #0A8CBB 83.29%);
	color: #ffffff;
}
.body-text {
	padding-top: 3em;
	padding-bottom: 3em;
	text-align: justify;
}
.body-text p {
	padding: 0 3em;
}
@media screen and (max-width: 768px) {
	.body-text p {
		padding: 0;
	}
}
.external-content {
	background: #fafafa;
	color: #333;
	min-height: 97vh;
}
.accordion {
  border: 1px solid #bdbdbd;
}

.accordion .accordion-item .title {
  background-color: #FFFFFF;
  color: #333;
  cursor: pointer;
  padding: 12px;
  width: 100%;
  text-align: left;
  border: 1px solid #bdbdbd;
  outline: none;
  transition: 0.4s;
  padding-left: 2em;
}
.accordion .accordion-item .title:after {
  content: '\2795';
  /* Unicode character for "plus" sign (+) */
  font-size: 13px;
  color: #fff;
  float: right;
  margin-left: 5px;
}
.accordion .accordion-item .panel {
  max-height: 0;
  transition: max-height 0.15s ease-out;
  overflow: hidden;
}
.accordion .accordion-item.active .title {
  background-color: #00b0f0;
  color: #fff;
}
.accordion .accordion-item.active .title:after {
  content: '\2796';
  /* Unicode character for "minus" sign (-) */
}
.accordion .accordion-item.active .panel {
  max-height: 500px;
  transition: max-height 0.25s ease-in;
  padding: 1em 2em;
  background: #f2f2f2;
}

